import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './CreatePost.scss';
import { Link, Navigate } from 'react-router-dom';
import {
    LogoIcon,
    CloseIcon,
    GalleryIcon,
    UnsplashIcon,
    BackIcon,
    SearchIcon,
    TagIcon,
    RemoveIcon,
    GalleryExportIcon,
    ArrowNextIcon,
    ArrowBackIcon,
    BoldIcon,
    ItalicIcon,
    H1Icon,
    H2Icon,
    H3Icon,
    CodeBlockIcon,
    BulletListIcon,
    NumberedListIcon

} from '../../icons';
import {
    BubbleMenu,
    EditorContent,
    FloatingMenu,
    useEditor,
} from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Document from '@tiptap/extension-document'
import Dropcursor from '@tiptap/extension-dropcursor'
import Image from '@tiptap/extension-image'
import Paragraph from '@tiptap/extension-paragraph'
import Text from '@tiptap/extension-text'
import Placeholder from '@tiptap/extension-placeholder'
import { Drawer } from '@mui/material';

export const CreatePost = () => {
    const [user, setUser] = useState(null);
    const [tags, setTags] = useState([]);
    const [open, setOpen] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [isActive, setIsActive] = useState(false);
    const [enabled, setEnabled] = useState(false)
    const [imageUrl, setImageUrl] = useState(null);
    const [unsplashActive, setUnsplashActive] = useState(false);
    const [blockIndex, setBlockIndex] = useState(0);
    const [searchResults, setSearchResults] = useState([]);
    const imagesToShow = searchResults.slice(blockIndex * 9, blockIndex * 9 + 9);
    const showMoreButton = blockIndex * 3 + 3 < searchResults.length;
    const [redirect, setRedirect] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');

    const handleChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const toggleDrawer = (newOpen) => () => {
        setOpen(newOpen);
    };

    useEffect(() => {
        const storedUser = localStorage.getItem("user");
        if (storedUser) {
            setUser(JSON.parse(storedUser));
        }
    }, []);;

    const createPost = () => {
        //        '   
        const selectedTags = tags.filter((tag) => tag.selected).map((tag) => ({
            id: tag.id,
            name: tag.name,
        }));
        //   ;  imageUrl  ,    
        const postImage = imageUrl ? imageUrl : 'https://i.imgur.com/L29x4vq.png';
        //  POST-    
        axios.post('https://04cb5470549a62ec.mokky.dev/posts', {
            title: editorTitle.getText(), //     
            text: editor.getHTML(), //      HTML  
            date: new Date().toISOString(), //      ISO
            user_id: user.id, //  ID  
            image: postImage, //   
            tags: selectedTags, //   
        }).then(response => {
            //    ,   
            console.log('Post created successfully:', response.data);
            //     
            setRedirect(true);
        }).catch(error => {
            //       
            console.error('Error creating post:', error);
        });
    };

    useEffect(() => {
        const fetchTags = async () => {
            try {
                const response = await axios.get(`https://04cb5470549a62ec.mokky.dev/tags`);
                const updatedTags = response.data.map((tag) => ({
                    ...tag,
                    display: 'none',
                    selected: false,
                }));
                setTags(updatedTags);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };
        fetchTags();
    }, []);

    useEffect(() => {
        const filteredTags = tags.map((tag) => ({
            ...tag,
            display: tag.name.toLowerCase().includes(searchTerm.toLowerCase()) ? 'flex' : 'none',
        }));
        setTags(filteredTags);
    }, [searchTerm]);

    useEffect(() => {
        document.addEventListener('click', handleClickOutside);
        return () => {
            document.removeEventListener('click', handleClickOutside);
        };
    }, []);


    const handleTagSelection = (tagId) => {
        setTags((prevTags) =>
            prevTags.map((tag) =>
                tag.id === tagId ? { ...tag, selected: !tag.selected } : tag
            )
        );
    };

    const removeTag = (tagId) => {
        setTags((prevTags) =>
            prevTags.map((tag) =>
                tag.id === tagId ? { ...tag, selected: false } : tag
            )
        );
    };
    //     
    const editorTitle = useEditor({
        extensions: [
            StarterKit,
            Placeholder.configure({
                placeholder: '',
            })
        ],
        content: '<h1></h1>',
        onUpdate: ({ editor }) => {
            checkEditorsContent();
        }
    });

    const editor = useEditor({
        extensions: [
            StarterKit,
            Document,
            Paragraph,
            Text,
            Image,
            Dropcursor,
            Placeholder.configure({
                placeholder: '  ...',
            })
        ],
        content: '<p></p>',
        onUpdate: ({ editor }) => {
            checkEditorsContent();
        }
    });


    const toggleActive = () => {
        setIsActive(true);
    };
    const toggleUnsplashActive = () => {
        setUnsplashActive(true);
    }
    const handleClickOutside = (event) => {
        if (!event.target.closest('.icon-menu')) {
            setIsActive(false);
        }
    };
    const addImage = () => {
        const url = window.prompt('URL')

        if (url) {
            editor.chain().focus().setImage({ src: url }).run()
        }
    }

    const handleSpanClick = () => {
        const url = window.prompt(' URL :');

        if (url !== null) {
            if (url.trim() !== '') {
                setImageUrl(url);
                console.log(imageUrl)
            } else {
                setImageUrl(null);
                console.log(imageUrl)
            }
        }
    };
    const handleAddTag = async () => {
        // ,           
        if (searchTerm.trim() !== '') {
            //      ,   - 
            const capitalizedTagName = searchTerm.charAt(0).toUpperCase() + searchTerm.slice(1);

            try {
                //  POST-      
                const response = await axios.post('https://04cb5470549a62ec.mokky.dev/tags', {
                    name: capitalizedTagName
                });

                // ,     ( 201)
                if (response.status === 201) {
                    //      
                    setTags(prevTags => [
                        ...prevTags,
                        {
                            id: response.data.id,
                            name: response.data.name,
                            display: 'flex', //   display
                            selected: false //    
                        }
                    ]);
                    //      
                    setSearchTerm('');
                } else {
                    //    ,     
                    console.error('Failed to add tag:', response);
                }
            } catch (error) {
                //         
                console.error('Error adding tag:', error);
            }
        }
    };

    const checkEditorsContent = () => {
        const isTitleEmpty = editorTitle && editorTitle.getText().length === 0;
        const isContentEmpty = editor && editor.getText().length === 0;
        // console.log(!isTitleEmpty)
        setEnabled(!isTitleEmpty && !isContentEmpty);
    };

    useEffect(() => {
        checkEditorsContent();
    }, [editorTitle, editor]);

    const searchUnsplashImages = async () => {
        try {
            //  GET-  API Unsplash      
            const response = await axios.get(`https://api.unsplash.com/search/photos?query=${searchQuery}&client_id=d75A_CGFOPD6kE5oc7dX-UEODZ6hzAUb-O6Z0USyGXw`);
            //    
            setSearchResults(response.data.results);
        } catch (error) {
            //         
            console.error('Error searching images:', error);
        }
    };

    const handleSearchInputChange = (event) => {
        setSearchQuery(event.target.value);
    };

    const handleSearchSubmit = (event) => {
        event.preventDefault();
        searchUnsplashImages();
    };
    const addImageToEditor = (url) => {
        editor.chain().focus().setImage({ src: url }).run();
        setUnsplashActive(false);
        setSearchQuery('');
        setSearchResults([]);
    };

    const handleShowMore = () => {
        setBlockIndex((prevIndex) => prevIndex + 1);
    };
    const handleShowPrev = () => {
        setBlockIndex((prevIndex) => prevIndex - 1);
    };
    if (redirect) {
        return <Navigate to="/falts/" />;
    }
    const DrawerList = (
        <div className='drawer-list'>
            <div className='drawer-title'>
                <div className='drawer-title-icon'>
                    <BackIcon />
                </div>
                <p> </p>
            </div>
            <div className='drawer-tags'>
                <p> </p>
                <div className='search'>
                    <SearchIcon />
                    <input type="text"
                        placeholder='...'
                        value={searchTerm}
                        onChange={handleChange} />
                </div>
                {searchTerm === '' ? (
                    <div className='results'>
                               .
                    </div>
                ) : (
                    <>
                        <div className='add-tag' onClick={handleAddTag}>
                            <TagIcon />
                            <p>  {searchTerm.charAt(0).toUpperCase() + searchTerm.slice(1)}</p>
                        </div>
                        <div className='divider' />
                        <div className='results'>
                            <div className='tags'>
                                <div className='menu'>
                                    {tags.map((tag) => (
                                        <div key={tag.id} className='tag' style={{ display: tag.display }}>
                                            <input
                                                type="checkbox"
                                                id={tag.id}
                                                checked={tag.selected}
                                                onChange={() => handleTagSelection(tag.id)}
                                            />
                                            <label htmlFor={tag.id}>{tag.name}</label>
                                        </div>
                                    ))}
                                    <div className='fade-top'></div>
                                    <div className='fade-bot'></div>
                                </div>
                            </div>
                        </div>
                    </>
                )}
                <div className='selected-tags'>
                    {tags.filter((tag) => tag.selected).map((tag) => (
                        <div key={tag.id} className='tag'>
                            <p>{tag.name}</p>
                            <div className='remove-tag' onClick={() => removeTag(tag.id)}>
                                <RemoveIcon />
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            <div className='drawer-image'>
                <p className='title'> </p>
                <p className='description'>   ,       .</p>
                {imageUrl ? (
                    <>
                        <div className='image'>
                            <img src={imageUrl} alt="" />
                        </div>
                    </>
                ) : (
                    <>
                        <div className='add-image'>
                            <GalleryExportIcon />
                            <p>  <span onClick={handleSpanClick}></span>   </p>
                        </div>
                    </>
                )
                }
            </div >
            <div className='drawer-button'>
                {tags.filter((tag) => tag.selected).length > 0 ?
                    <button className='btn' onClick={createPost}></button>
                    :
                    <button className='btn' disabled></button>
                }
            </div>
        </div >
    )
    return (
        <>
            <header>
                <nav className='nav-left'>
                    <Link to={'/falts/'} style={{ textDecoration: 'none', height: '24px' }}>
                        <LogoIcon />
                    </Link>
                </nav>
                <nav className='nav-right'>
                    {enabled ? (
                        <button className='create-btn' onClick={toggleDrawer(true)}></button>
                    ) : (
                        <button className='create-btn' disabled onClick={toggleDrawer(true)}></button>
                    )}
                    <Drawer anchor={'right'} open={open} onClose={toggleDrawer(false)}>
                        {DrawerList}
                    </Drawer>
                </nav>

            </header>
            <div className='create-post'>
                <div className='content'>
                    {/* ,    */}
                    {editor && (
                        //   BubbleMenu,   
                        <BubbleMenu
                            className="bubble-menu"
                            tippyOptions={{ duration: 100 }} //   Tippy.js
                            editor={editor} // '   
                        >
                            {/*      */}
                            <button
                                onClick={() => editor.chain().focus().toggleBold().run()} //      
                                className={editor.isActive('bold') ? 'is-active' : ''} //   'is-active',    
                            >
                                <BoldIcon /> {/*     */}
                            </button>

                            {/*      */}
                            <button
                                onClick={() => editor.chain().focus().toggleItalic().run()} //      
                                className={editor.isActive('italic') ? 'is-active' : ''} //   'is-active',    
                            >
                                <ItalicIcon /> {/*     */}
                            </button>

                            {/*      1 */}
                            <button
                                onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()} //       1
                                className={editor.isActive('heading', { level: 1 }) ? 'is-active' : ''} //   'is-active',    1 
                            >
                                <H1Icon /> {/*     1 */}
                            </button>

                            {/*      2 */}
                            <button
                                onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()} //       2
                                className={editor.isActive('heading', { level: 2 }) ? 'is-active' : ''} //   'is-active',    2 
                            >
                                <H2Icon /> {/*     2 */}
                            </button>

                            {/*      3 */}
                            <button
                                onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()} //       3
                                className={editor.isActive('heading', { level: 3 }) ? 'is-active' : ''} //   'is-active',    3 
                            >
                                <H3Icon /> {/*     3 */}
                            </button>

                            {/*      */}
                            <button
                                onClick={() => editor.chain().focus().toggleCodeBlock().run()} //      
                                className={editor.isActive('codeBlock') ? 'is-active' : ''} //   'is-active',    
                            >
                                <CodeBlockIcon /> {/*     */}
                            </button>

                            {/*      */}
                            <button
                                onClick={() => editor.chain().focus().toggleBulletList().run()} //      
                                className={editor.isActive('bulletList') ? 'is-active' : ''} //   'is-active',    
                            >
                                <BulletListIcon /> {/*     */}
                            </button>

                            {/*      */}
                            <button
                                onClick={() => editor.chain().focus().toggleOrderedList().run()} //      
                                className={editor.isActive('orderedList') ? 'is-active' : ''} //   'is-active',    
                            >
                                <NumberedListIcon /> {/*     */}
                            </button>
                        </BubbleMenu>
                    )}


                    {editor && (
                        <FloatingMenu className="floating-menu" tippyOptions={{ duration: 100 }} editor={editor}>
                            <div className={`icon-menu ${isActive ? 'active' : ''}`} onClick={toggleActive}>
                                <div className='icon plus' >
                                    <CloseIcon />
                                </div>
                                <div className='icon image' onClick={addImage}>
                                    <GalleryIcon />
                                </div>
                                <div className='icon image' onClick={toggleUnsplashActive}>
                                    <UnsplashIcon />
                                </div>
                            </div>
                        </FloatingMenu>

                    )}

                    <div className='editors'>
                        <EditorContent editor={editorTitle} />
                        <EditorContent editor={editor} />
                        {unsplashActive && (
                            <div className="search-container">
                                <form onSubmit={handleSearchSubmit}>
                                    <div className='search'>
                                        <input
                                            type="text"
                                            value={searchQuery}
                                            onChange={handleSearchInputChange}
                                            placeholder="   Unsplash"
                                        />
                                    </div>
                                    <button type="submit"></button>
                                </form>
                                {showMoreButton && (
                                    <div className='show-more'>
                                        <button onClick={handleShowPrev}><ArrowBackIcon /></button>
                                        <button onClick={handleShowMore}><ArrowNextIcon /></button>
                                    </div>
                                )}
                                <div className="search-results">
                                    {imagesToShow.map((image) => (
                                        <div key={image.id} className="image-result" onClick={() => addImageToEditor(image.urls.full)}>
                                            <img src={image.urls.small} alt={image.alt_description} />
                                        </div>
                                    ))}

                                </div>

                            </div>
                        )}
                    </div>
                </div>
            </div>
        </>
    );
};
